export class Vector3 {
    x: number = 0;
    y: number = 0;
    z: number = 0;

    get length() {
        return (this.x ** 2 + this.y ** 2 + this.z ** 2) ** (1 / 2);
    }

    set(a?: number, b?: number, c?: number) {

        if (a !== undefined) {
            this.x = a;
        }

        if (b !== undefined) {
            this.y = b;
        }

        if (c !== undefined) {
            this.z = c;
        }

    }

    add(vec3: Vector3) {
        this.x += vec3.x;
        this.y += vec3.y;
        this.z += vec3.z;

    }

    multiply(val: number) {
        this.x *= val;
        this.y *= val;
        this.z *= val;
    }

    resetToZero() {
        this.x = 0;
        this.y = 0;
        this.z = 0;
    }

    equal(out: Vector3, fixed: number = 3) {
        return (out.x.toFixed(fixed) === this.x.toFixed(fixed))
            && (out.y.toFixed(fixed) === this.y.toFixed(fixed))
            && (this.z.toFixed(fixed) === this.z.toFixed(fixed));
    }
}
